.App {
  text-align: center;
  background-image: -webkit-gradient(
    linear,
    right bottom, left top,
    from(#159957),
    color-stop(#008e7e),
    color-stop(#00809b),
    color-stop(#006da5),
    to(#155799)
  );
  background-image: -webkit-linear-gradient(
    right bottom,
    #159957,
    #008e7e,
    #00809b,
    #006da5,
    #155799
  );
  background-image: linear-gradient(
    to left top,
    #159957,
    #008e7e,
    #00809b,
    #006da5,
    #155799
  );
  display: flex;
  flex-wrap: wrap;
  min-height: 100vh;
}

.animation {
  margin: 200px auto;
  width: 200px;
  text-align: center;
}

.animation > div {
  width: 50px;
  height: 50px;
  opacity: 0.6;
  background-color: #006da5;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: circle 1.5s infinite ease-in-out both;
          animation: circle 1.5s infinite ease-in-out both;
}

.animation .circle1 {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}

.animation .circle2 {
  -webkit-animation-delay: -0.25s;
          animation-delay: -0.25s;
}

@-webkit-keyframes circle {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes circle {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
